import { AppLanguage } from '../../../../../app.language';
import { FriendsEnemiesEventsLocalization } from './friends.enemies.events';
import {DiceD6} from '../../../../../service/dices/dice.d6';

export class FriendsEnemiesEventsLocalizationRu implements FriendsEnemiesEventsLocalization {
  getHeader() {
    return '  ';
  }

  getFriendEnemy(roll: number) {
    if (roll < 6) {
      return '   ';
    } else {
      return '   ';
    }
  }

  getFriendsEnemiesGender(roll: number) {
    if (roll % 2 === 0) {
      return ' ';
    } else {
      return ' ';
    }
  }

  getGeneratedPerson(roll: number) {
    if (roll % 2 === 0) {
      return '    :';
    } else {
      return '    ';
    }
  }

  getFriend() {
    return '    ';
  }

  getFriendByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '    /';
      case 2:
        return '    /';
      case 3:
        return '/';
      case 4:
        return '/  ';
      case 5:
        return ' () (  )';
      case 6:
        return '  (  )';
      case 7:
        return '  /';
      case 8:
        return '';
      case 9:
        return '  ';
      case 10:
        return '   ';
    }
  }

  getEnemy() {
    return ' ';
  }

  getEnemyByRoll(roll: number) {
    switch (roll) {
      case 1:
        return ' ';
      case 2:
        return ' ()';
      case 3:
        return '';
      case 4:
        return ' ';
      case 5:
        return ',   ';
      case 6:
        return ',    ';
      case 7:
        return '  ';
      case 8:
        return ' ';
      case 9:
        return '  ';
      case 10:
        return ' ';
    }
  }

  getCause() {
    return ' ';
  }

  getCauseByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '     ';
      case 2:
        return '  ,   ';
      case 3:
        return '  ';
      case 4:
        return '       ';
      case 5:
        return '  : ' + this.getDisability();
      case 6:
        return ' - ,    ';
      case 7:
        return '    ';
      case 8:
        return '   -';
      case 9:
        return '    ';
      case 10:
        return '   ';
    }
  }

  private getDisability() {
    const d6 = new DiceD6();
    switch (d6.roll()) {
      case 1:
      case 2:
        return ' ';
      case 3:
      case 4:
        return ' ';
      case 5:
      case 6:
        return '  ';
    }
  }

  getWhosFracked() {
    return ' ';
  }

  getWhosFrackedByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
      case 3:
      case 4:
        return '  ';
      case 5:
      case 6:
      case 7:
        return '  ';
      case 8:
      case 9:
      case 10:
        return '  ';
    }
  }

  getWhatGonnaDo() {
    return '  ,     ';
  }

  getWhatGonnaDoByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
        return '      ';
      case 3:
      case 4:
        return '    ';
      case 5:
      case 6:
        return '      ';
      case 7:
      case 8:
        return '  ';
      case 9:
      case 10:
        return '  ';
    }
  }

  getWhatHeCanThrow() {
    return '      ';
  }

  getWhatHeCanThrowByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
      case 3:
        return ' ';
      case 4:
      case 5:
        return '    ';
      case 6:
      case 7:
        return '  ';
      case 8:
        return '  ';
      case 9:
        return '  ';
      case 10:
        return '  ';
    }
  }

  getLanguage() {
    return AppLanguage.RUSSIAN;
  }
}
