import { EthnicLocalization } from './ethnic';
import { AppLanguage } from '../../../../app.language';

export class EthnicLocalizationRu implements EthnicLocalization {
  getHeader() {
    return ' ';
  }

  getOrigin() {
    return '';
  }

  getOriginByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '';
      case 2:
        return '-';
      case 3:
        return ' ';
      case 4:
        return '/- ';
      case 5:
        return '';
      case 6:
        return '';
      case 7:
        return '/';
      case 8:
        return '';
      case 9:
        return '/-';
      case 10:
        return '-/';
      default:
        return '-   ';
    }
  }

  getLanguages() {
    return '';
  }

  getLanguagesByRoll(roll: number) {
    switch (roll) {
      case 1:
        return ['', '', '', '', '', ''];
      case 2:
        return [''];
      case 3:
        return ['', ' '];
      case 4:
        return ['', '', '', '', '', ''];
      case 5:
        return ['', '', '', '', '',
          '', '', '', '', ''];
      case 6:
        return ['', ''];
      case 7:
        return ['', ''];
      case 8:
        return ['', '', '', '',
          '', '', ''];
      case 9:
        return ['', ''];
      case 10:
        return ['', '', '', '', ''];
      default:
        return '-   ';
    }
  }


  getLanguage() {
    return AppLanguage.RUSSIAN;
  }
}
