import { AppLanguage } from '../../../../../app.language';
import { ProblemsWinsEventsLocalization } from './problems.wins.events';
import { DiceD10 } from '../../../../../service/dices/dice.d10';

export class ProblemsWinsEventsLocalizationRu implements ProblemsWinsEventsLocalization {
  private d10 = new DiceD10();

  getHeader() {
    return ' ,  ';
  }

  getDisaster() {
    return ' ';
  }

  getDisasterByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '   .   '
          + this.getFinancial() + ' . ' +
          '       ,    , ' +
          '     .';
      case 2:
        return ':    , ' +
          '     (  )   '
          + this.getImprisonment() + ' .';
      case 3:
        return '   :      ' +
          '   . ' +
          '     1  REF.';
      case 4:
        return ':    : ' +
          this.getBetrayal();
      case 5:
        return ' :     . ' +
          this.getAccident();
      case 6:
        return ',    : ' +
          '  -  . ' + this.getRelativeKilled();
      case 7:
        return ' :  . ' +
          this.getFalseAccusation();
      case 8:
        return '  :     ' +
          '(    ,    (  )). ' +
          this.getHuntedLaw();
      case 9:
        return '   :    . ' +
          this.getHuntedCorporation();
      case 10:
        return '   :  - ' +
          this.getIncapacitation();
    }
  }

  private getFinancial() {
    return this.d10.roll() * 100;
  }

  private getImprisonment() {
    return this.d10.roll();
  }

  private getBetrayal() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
        return ' .';
      case 4:
      case 5:
      case 6:
      case 7:
        return '   .';
      case 8:
      case 9:
      case 10:
        return '   :  ' +
          '      (  ).';
    }
  }

  private getAccident() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
      case 4:
        return '       -5   ATT.';
      case 5:
      case 6:
        return '    ' + this.d10.roll() + '    .';
      case 7:
      case 8:
        return '  ' + this.d10.roll() + '     .';
      case 9:
      case 10:
        return '    (   8  10), ' +
          '        .';
    }
  }

  private getRelativeKilled() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
      case 4:
      case 5:
        return '    .';
      case 6:
      case 7:
      case 8:
        return '  .';
      case 9:
      case 10:
        return ',      ,     .';
    }
  }

  private getFalseAccusation() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
        return '  .';
      case 4:
      case 5:
        return '  .';
      case 6:
      case 7:
      case 8:
        return '  .';
      case 9:
        return '  .';
      case 10:
        return '    .';
    }
  }

  private getHuntedLaw() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
        return '     .';
      case 4:
      case 5:
      case 6:
        return '    .';
      case 7:
      case 8:
        return '    .';
      case 9:
      case 10:
        return '      .';
    }
  }

  private getHuntedCorporation() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
        return '   .';
      case 4:
      case 5:
      case 6:
        return '        .';
      case 7:
      case 8:
        return '           .';
      case 9:
      case 10:
        return '      ,    .';
    }
  }

  private getIncapacitation() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
        return ' -  ,  - ' +
          ' -  1  REF.';
      case 4:
      case 5:
      case 6:
      case 7:
        return ' -  ; ' +
          '      . ' +
          ' 1   COOL .';
      case 8:
      case 9:
      case 10:
        return '  .   , ' +
          '     . ' +
          ' 1   COOL  1  REF.';
    }
  }

  getLucky() {
    return ' ';
  }

  getLuckyByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '      . ' +
          this.getPowerfulConnection();
      case 2:
        return ' :  ' + this.getFinancial() +
          '  .';
      case 3:
        return '     !   ' + this.getFinancial() +
          '  .';
      case 4:
        return '   (). ' +
          '      (Martial Arts) +2 ' +
          '( ),      1.';
      case 5:
        return '  :   +1    ' +
          '   INT,    ' +
          '+2  INT.';
      case 6:
        return '       .';
      case 7:
        return '    . ' +
          '        , ' +
          '  (Family) +2.';
      case 8:
        return '    .     ' +
          '       (StreetWise) +2 ' +
          '     .';
      case 9:
        return '    (  .   , ?). ' +
          '      ,  ' +
          '  (Family) +2.   ' +
          ' !';
      case 10:
        return '   .  ' +
          '+1     ,   ' +
          '   +2.';
    }
  }

  private getPowerfulConnection() {
    switch (this.d10.roll()) {
      case 1:
      case 2:
      case 3:
      case 4:
        return '  .';
      case 5:
      case 6:
      case 7:
        return '  .';
      case 8:
      case 9:
      case 10:
        return ' .';
    }
  }

  getWhatGonnaDo() {
    return '   - ';
  }

  getWhatGonnaDoByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
        return ' ';
      case 3:
      case 4:
        return '      ';
      case 5:
      case 6:
        return '       !';
      case 7:
      case 8:
        return ' ,     ';
      case 9:
      case 10:
        return ',  ,  ,      ';
    }
  }

  getLanguage() {
    return AppLanguage.RUSSIAN;
  }
}
