import { FamilyLocalization } from './family';
import { AppLanguage } from '../../../../app.language';

export class FamilyLocalizationRu implements FamilyLocalization {

  getHeader() {
    return ' ';
  }

  getRanking() {
    return ' ';
  }

  getRankingByRoll(roll: number) {
    switch (roll) {
      case 1:
        return ' ';
      case 2:
        return ' ';
      case 3:
        return ' ';
      case 4:
        return ' ';
      case 5:
        return '';
      case 6:
        return ' ';
      case 7:
        return ' ';
      case 8:
        return '   ';
      case 9:
        return ' ';
      case 10:
        return '';
      default:
        return '-   ';
    }
  }

  getParents() {
    return '';
  }

  getParentsByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
      case 3:
      case 4:
      case 5:
      case 6:
        return '  ';
      case 7:
      case 8:
      case 9:
      case 10:
        return '-      ';
      default:
        return '-   ';
    }
  }

  getHappedEvent() {
    return '-    ';
  }

  getHappedEventByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '    ';
      case 2:
        return '   - ';
      case 3:
        return '   ';
      case 4:
        return '        ';
      case 5:
        return '   ()  ';
      case 6:
        return '  ,   ';
      case 7:
        return '     ';
      case 8:
        return ' ()      () ';
      case 9:
        return '     ';
      case 10:
        return '     ';
      default:
        return '-   ';
    }
  }

  getStatus() {
    return ' ';
  }

  getStatusByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
      case 3:
      case 4:
      case 5:
      case 6:
        return '        (   ())';
      case 7:
      case 8:
      case 9:
      case 10:
        return '   ,      ';
      default:
        return '-   ';
    }
  }

  getChildEnv() {
    return '';
  }

  getChildEnvByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '     ';
      case 2:
        return '     ';
      case 3:
        return '  ,     ';
      case 4:
        return ' ,    ';
      case 5:
        return '     ';
      case 6:
        return '   ';
      case 7:
        return '       ';
      case 8:
        return '   ';
      case 9:
        return '   ';
      case 10:
        return '     ';
      default:
        return '-   ';
    }
  }

  getTragedy() {
    return ' ';
  }

  getTragedyByRoll(roll: number) {
    switch (roll) {
      case 1:
        return '   - ';
      case 2:
        return '   -  ';
      case 3:
        return '        //';
      case 4:
        return '  ,   () ';
      case 5:
        return '   . ()  ';
      case 6:
        return '     (),     ';
      case 7:
        return '  - ,       ';
      case 8:
        return '     - ';
      case 9:
        return '     ,     ';
      case 10:
        return '   .     ,      ';
      default:
        return '-   ';
    }
  }

  getSibling() {
    return '  ';
  }

  getNumSiblingsByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
      case 3:
      case 4:
      case 5:
      case 6:
      case 7:
        return '  ' + roll + ' /';
      default:
        return '  ';
    }
  }

  getSiblingGenderByRoll(roll: number) {
    if (roll % 2 === 0) {
      return '';
    } else {
      return '';
    }
  }

  getSiblingAgeByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
      case 3:
      case 4:
      case 5:
        return ' ';
      case 6:
      case 7:
      case 8:
      case 9:
        return ' ';
      case 10:
        return ' ';
      default:
        return '-   ';
    }
  }

  getSiblingFeelingsByRoll(roll: number) {
    switch (roll) {
      case 1:
      case 2:
        return ' ';
      case 3:
      case 4:
        return '   ';
      case 5:
      case 6:
        return '   ';
      case 7:
      case 8:
        return '  ';
      case 9:
      case 10:
        return ' ';
      default:
        return '-   ';
    }
  }

  getLanguage() {
    return AppLanguage.ENGLISH;
  }
}
